import { Link, Switch, Route, useRouteMatch } from 'react-router-dom'

import './recomendation.scss'

import SaleCard from '../../components/saleCard/saleCard'

import Img1 from '../../assets/images/image1.png'
import Img2 from '../../assets/images/image2.png'
import Img3 from '../../assets/images/image3.png'
import Img4 from '../../assets/images/image4.png'

const Recomendation = () => {

    const { path, url } = useRouteMatch()

    return (
        <div className="recomendation-block container">
            <div className="recomendation-block-top">
                <Link to={`${url}`} className="recomendation-block-top__link">Просмотренные</Link>
                <Link to={`${url}/choose`} className="recomendation-block-top__link">Выбор редакции</Link>
                <Link to={`${url}/price`} className="recomendation-block-top__link">Сниженные цены</Link>
            </div>

            <Switch>
                    <Route exact path={`${path}`}>
                    <div className="recomendation-block-center">
                        <SaleCard
                            title='Светодиодная лампа с фронтальным стеклом 10шт'
                            publish='Вчера 22:55'
                            price='39 000 000 сум'
                            img={Img1}
                        />

                        <SaleCard
                            title='Коттедж 424 м² на участке 22.6 сот.'
                            publish='Вчера 22:55'
                            price='39 000 000 сум'
                            img={Img2}
                        />

                        <SaleCard
                            title='2-к квартира, 55 м², 3/5 эт.'
                            publish='Вчера 22:55'
                            price='39 000 000 сум'
                            img={Img3}
                        />

                        <SaleCard
                            title='Кровать с закроватным модулем+ матрас Вита 160'
                            publish='Вчера 22:55'
                            price='39 000 000 сум'
                            img={Img4}
                        />
                    </div>
                        <div className="recomendation-block-bottom">
                            <button className="recomendation-block-bottom__btn">
                                Покозать еще
                            </button>
                        </div>
                    </Route>
                    <div className="recomendation-block-bottom">
                    <button className='recomendation-block-bottom__btn'>more</button>
                    </div>  
                    <Route path={`${path}/choose`}>
                        <h1>Choose</h1>
                    </Route>
                    <Route path={`${path}/price`}>
                        <h1>Price</h1>
                    </Route>
            </Switch>

        </div>
    )
}

export default Recomendation;