import './addCard.scss'

const AddCard = ({title,img, addittional,info}) => {
    return(
        <div className={`add-card ${addittional ? 'additional' : ''}`}>
            <div className="add-card-left">
                <h3 className="add-card-left__title">{title}</h3>
                <a href="#" className="add-card-left__info">{info}</a>
            </div>

            <img src={img} alt="" className="add-card__img"/>

            <div className={`triangle ${addittional ? 'additional' : ''}`}></div>
            
        </div>
    )
}

export default AddCard;